
@font-face {
	font-family: 'icomoon';
	src:  url('../fonts/Icons/icomoon.eot');
	src:  url('../fonts/Icons/icomoon.eot') format('embedded-opentype'),
	  url('../fonts/Icons/icomoon.ttf') format('truetype'),
	  url('../fonts/Icons/icomoon.woff') format('woff'),
	  url('../fonts/Icons/icomoon.svg') format('svg');
	font-weight: normal;
	font-style: normal;
	font-display: block;
  }
  
  i {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'icomoon' !important;
	speak: never;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
  
	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
  }
  
  .icon-full-out:before {
	content: "\e90e";
  }
  .icon-star:before {
	content: "\e90d";
  }
  .icon-send:before {
	content: "\e90c";
  }
  .icon-full:before {
	content: "\e909";
  }
  .icon-shield:before {
	content: "\e90a";
  }
  .icon-alarm:before {
	content: "\e90b";
  }
  .icon-arr-r2:before {
	content: "\e907";
  }
  .icon-arr-l2:before {
	content: "\e908";
  }
  .icon-arr-l:before {
	content: "\e905";
  }
  .icon-arr-r:before {
	content: "\e906";
  }
  .icon-search:before {
	content: "\e903";
  }
  .icon-fb:before {
	content: "\e900";
  }
  .icon-vk:before {
	content: "\e901";
  }
  .icon-tg:before {
	content: "\e902";
  }
  .icon-edit:before {
	content: "\e904";
  }
  .icon-apple:before {
	content: "\eabe";
  }
  
  
